import app from 'flarum/forum/app';
import ItemList from 'flarum/common/utils/ItemList';
import AbstractPost from 'flarum/forum/components/AbstractPost';
import Avatar from 'flarum/common/components/Avatar';
import Comment from 'flarum/forum/components/Comment';
import PostUser from 'flarum/forum/components/PostUser';
import PostMeta from 'flarum/forum/components/PostMeta';
import classList from 'flarum/common/utils/classList';
import MessageControls from '../utils/MessageControls';
/**
 * The `Post` component displays a single post. The basic post template just
 * includes a controls dropdown; subclasses must implement `content` and `attrs`
 * methods.
 */
export default class Message extends AbstractPost {
  oninit(vnode) {
    super.oninit(vnode);
  }
  user() {
    return this.attrs.message.user();
  }
  controls() {
    return MessageControls.controls(this.attrs.message, this).toArray();
  }
  freshness() {
    return this.attrs.message.freshness;
  }
  createdByStarter() {
    return false;
  }
  onbeforeupdate(vnode) {
    return super.onbeforeupdate(vnode);
  }
  onupdate(vnode) {
    super.onupdate(vnode);
  }
  elementAttrs() {
    const message = this.attrs.message;
    const attrs = super.elementAttrs();
    attrs.className = classList(attrs.className || null, 'Message', {
      'Post--renderFailed': message.renderFailed(),
      revealContent: false,
      editing: false
    });
    return attrs;
  }
  header() {
    return super.header();
  }
  content() {
    return super.content().concat([m(Comment, {
      headerItems: this.headerItems(),
      cardVisible: false,
      isEditing: false,
      isHidden: false,
      contentHtml: this.attrs.message.contentHtml(),
      user: this.attrs.message.user()
    })]);
  }
  classes(existing) {
    return super.classes(existing);
  }
  actionItems() {
    return super.actionItems();
  }
  footerItems() {
    return super.footerItems();
  }
  sideItems() {
    return super.sideItems();
  }
  avatar() {
    return this.attrs.message.user() ? m(Avatar, {
      user: this.attrs.message.user(),
      className: "Post-avatar"
    }) : '';
  }
  headerItems() {
    const items = new ItemList();
    const message = this.attrs.message;
    items.add('user', m(PostUser, {
      post: message
    }), 100);
    items.add('meta', m(PostMeta, {
      post: message,
      permalink: () => {
        const dialog = message.dialog();
        if (!dialog) {
          return null;
        }
        return app.forum.attribute('baseOrigin') + app.route.dialog(dialog, message.number());
      }
    }));
    return items;
  }
}
flarum.reg.add('flarum-messages', 'forum/components/Message', Message);